body {
  cursor: none;
  background: #202930 0 0 / cover;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}

.circle {
  pointer-events: none;
  z-index: 99999999;
  cursor: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  transition: transform 50ms linear;
  position: fixed;
  top: 0;
  left: 0;
}

.montserrat {
  font-optical-sizing: auto;
  scroll-behavior: unset;
  font-family: Montserrat, serif;
  font-weight: 500;
}

#loader {
  z-index: 9999;
  background: #202930;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  border: 10px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.scroll-progress-container {
  z-index: 9999;
  background: #ffffff1a;
  width: 5px;
  height: 10vh;
  position: fixed;
  top: 320px;
  right: 10px;
}

.scroll-progress-bar {
  background: #2f73b9;
  width: 100%;
  transition: height .2s ease-in-out;
}

.header {
  justify-content: space-between;
  align-items: center;
  font-family: Titillium Web, serif;
  display: flex;
}

.logo {
  width: 60px;
  height: auto;
  transition: transform .3s;
  display: block;
}

.logo:hover {
  transform: scale(1.05);
}

.nav-items {
  padding: 0 20px;
}

.nav-items > ul {
  margin: 0;
  font-size: 24px;
  list-style-type: none;
  display: flex;
}

.nav-items ul li {
  align-items: center;
  margin: 10px;
  padding: 10px;
  display: flex;
  position: relative;
}

.nav-items ul li a {
  color: #2f73b9;
  cursor: none;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav-items ul li a i {
  vertical-align: middle;
  margin-right: 12px;
  font-size: 1.4em;
}

.nav-items ul li a:hover {
  color: #a0d6ee;
}

.nav-items ul li i {
  font-size: 20px;
}

#main-content {
  height: 100vh;
  padding: 10px;
}

.content-section {
  z-index: 0;
  position: relative;
}

.video-bg {
  z-index: -1;
  width: 100%;
  height: 88%;
  position: absolute;
  top: 12%;
  left: 0;
  overflow: hidden;
}

.video-bg video {
  object-fit: cover;
  object-position: 80% center;
  width: 50%;
  height: 90%;
  position: absolute;
  top: 70px;
  right: 0;
}

.hamburger {
  cursor: pointer;
  color: #7b7b7b;
  cursor: none;
  font-size: 30px;
  display: none;
  position: fixed;
  top: 4px;
  right: 30px;
}

.overlay {
  z-index: 999;
  background: #0006;
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  padding: 20px;
  display: block;
}

.no-scroll {
  position: fixed;
  overflow: hidden;
}

@media (width <= 379px) {
  #projects {
    padding-bottom: 500px;
  }

  #contacts {
    margin-top: 50px;
    padding-top: 30px;
  }

  .contact {
    width: 100%;
    padding: 20px;
  }

  .heading {
    font-size: 1.8rem;
  }

  .input-box {
    flex-direction: column;
    gap: 15px;
  }

  .input-box input, textarea, .btn {
    width: 100%;
  }
}

@media (width <= 768px) {
  .logo {
    width: 50px;
    height: auto;
    margin-top: 7px;
    transition: transform .3s;
    display: block;
  }

  .logo:hover {
    transform: scale(1.05);
  }

  .nav-items {
    display: none;
  }

  .hamburger {
    color: #36454f;
    filter: brightness(1.5);
    z-index: 102;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    display: flex;
    right: 10px;
  }

  .hamburger:hover {
    filter: brightness();
    background: #a0d6ee;
  }

  .video-bg video {
    object-position: center center;
    width: 100%;
    height: 70%;
    top: 210px;
  }

  .nav-items.open {
    text-align: center;
    z-index: 10;
    background: #fff;
    flex-direction: column;
    align-items: flex-end;
    width: 250px;
    height: 100vh;
    padding: 0;
    display: flex;
    position: fixed;
    top: 0;
    left: auto;
    right: 0;
    overflow: hidden;
  }

  .hamburger.active {
    color: red;
  }

  .nav-items.open + #main-content {
    padding-top: 60px;
  }

  .nav-items ul {
    flex-direction: column;
    width: 100%;
    padding: 0;
  }

  .nav-items > ul > li {
    margin: 10px 0;
    padding: 10px 20px;
    top: 100px;
    left: 40px;
  }

  .nav-items ul li a {
    color: #2f73b9;
    font-size: 22px;
    font-weight: 500;
    transition: color .3s;
  }

  .nav-items ul li a i {
    margin-right: 10px;
    font-size: 1.2em;
  }

  #main-content {
    height: 100vh;
    padding: 10px;
  }
}

html, body {
  overflow-x: hidden;
}

.card-container {
  flex-direction: column;
  align-items: flex-start;
  gap: 35px;
  width: auto;
  max-width: 500px;
  margin-top: 55px;
  margin-left: 10px;
  padding-right: 20px;
  display: flex;
  position: relative;
}

.video {
  pointer-events: none;
  background: #202930;
  width: 400px;
  height: auto;
  position: absolute;
  top: 50%;
  right: 50px;
  transform: translateY(-50%);
}

.card {
  box-sizing: border-box;
  background-color: #f5f5f5;
  border-radius: 12px;
  max-width: 100%;
  padding: 20px 30px;
  box-shadow: 0 4px 10px #0000001a;
}

.greeting-card {
  text-align: left;
}

.greeting-content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.wave {
  margin-right: 20px;
  font-size: 2.1rem;
}

.text-right {
  text-align: right;
}

.hello-text {
  text-align: left;
  color: #878b97;
  margin: 0;
  font-size: 1rem;
}

.name {
  text-align: left;
  color: #1c3246;
  margin: 0;
  font-size: 2rem;
  font-weight: bold;
}

.card h2 {
  text-align: left;
  white-space: nowrap;
  margin: 0 0 5px;
  font-size: 1.89rem;
  font-weight: bold;
}

.card .highlight {
  color: #2f73b9;
}

.card h2:nth-child(2) {
  color: #1c3246;
}

.montserrat {
  font-optical-sizing: auto;
  scroll-behavior: unset;
  font-family: Montserrat, serif;
  font-weight: 500;
}

button {
  margin-top: 20px;
  font-family: Montserrat, serif;
}

.resume-link {
  color: #fff;
  letter-spacing: 1px;
  cursor: pointer;
  background-color: #2f73b9;
  border: none;
  border-radius: 8px;
  margin: 0;
  padding: 12px 24px;
  font-size: 1.1rem;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: inline-block;
  box-shadow: 0 2px 5px #0000001a;
}

.resume-link:hover {
  background-color: #1da1f280;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px #0003;
}

.resume-link:active {
  transform: translateY(0);
  box-shadow: 0 2px 5px #0000001a;
}

.pdf-viewer-overlay {
  z-index: 1000;
  background: #000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 10%;
  left: 0;
}

.pdf-viewer-container {
  background: #fff;
  border-radius: 8px;
  width: 80%;
  height: 90%;
  padding: 20px;
  position: relative;
}

.pdf-iframe {
  border: none;
  width: 100%;
  height: 100%;
}

.close-button {
  color: #fff;
  cursor: pointer;
  background: red;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 20px;
  position: absolute;
  top: -15px;
  right: -15px;
}

.social-media {
  justify-content: flex-start;
  gap: 10px;
  margin: 0;
  padding-bottom: 0;
  text-decoration: none;
  display: flex;
}

.social-icon {
  color: #fff;
  cursor: none;
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  font-size: 2rem;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.social-icon:first-child, .social-icon:nth-child(2) {
  color: #f5f5f5;
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  font-size: 2rem;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.social-icon:nth-child(3) {
  color: #fff;
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  font-size: 2rem;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.social-icon:first-child:hover {
  background-color: #454550;
  transform: scale(1.1);
}

.social-icon:nth-child(2):hover {
  background-color: #1da1f2;
  transform: scale(1.1);
}

.social-icon:nth-child(3):hover {
  background-image: linear-gradient(45deg, #feda75, #fa7e1e, #d62976, #962fbf, #4f5bd5);
  transform: scale(1.1);
}

.social-icon:nth-child(4):hover {
  background-color: #1da1f2;
  transform: scale(1.1);
}

