@media (width >= 769px) {
  .card-container {
    margin-left: 10%;
    margin-right: 10%;
  }
}

.projects-section {
  text-align: center;
  padding: 50px 20px;
}

.section-subtitle {
  color: #2f73b9;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0 0 20px;
  font-family: Titillium Web, serif;
  font-size: 3.7rem;
  font-weight: 700;
}

.projects-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  max-width: 1200px;
  margin: 70px auto 0;
  display: flex;
}

.project-card {
  text-align: center;
  background: linear-gradient(135deg, #f5f5f5, #e0e0e0);
  border-radius: 12px;
  width: 300px;
  height: 270px;
  padding: 30px 20px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 10px #a0d6eeb3, 0 7px 13px -3px #a0d6ee80, inset 0 -3px #a0d6ee66;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px #0006, 0 10px 20px -3px #0000004d, inset 0 -4px #0003;
}

.project-icon {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}

.project-card h3 {
  color: #1c3246;
  margin: 0 0 15px;
  font-size: 1.8rem;
  font-weight: 700;
}

.project-card p {
  color: #35373a;
  margin: 0;
  font-size: 1rem;
  line-height: 1.6;
}

@media (width <= 480px) {
  .project-card {
    width: 100%;
    max-width: 280px;
    padding: 15px;
  }

  .project-card h3 {
    font-size: 1.3rem;
  }

  .project-card p {
    font-size: .9rem;
  }
}

@media (width <= 768px) {
  .projects-container {
    flex-direction: column;
    align-items: center;
  }

  .project-card {
    width: 100%;
    max-width: 280px;
    height: auto;
    padding: 15px;
  }

  .project-icon {
    width: 60px;
    height: 60px;
  }

  .project-card h3 {
    font-size: 1.5rem;
  }

  .project-card p {
    font-size: 1rem;
  }
}

@media (width <= 992px) {
  .projects {
    flex-wrap: wrap;
    justify-content: center;
  }

  .projects .card {
    width: 30%;
  }
}

.contact-section {
  color: #f5f5f5;
  text-align: center;
  background: #202930;
  padding: 80px 40px;
}

.heading {
  color: #2f73b9;
  text-transform: uppercase;
  margin-bottom: 50px;
  font-family: Titillium Web, sans-serif;
  font-size: 3.7rem;
}

.contact-container {
  box-sizing: border-box;
  grid-template-columns: 1fr 2fr;
  align-items: start;
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  display: grid;
}

.contact-info {
  text-align: left;
  flex-direction: column;
  padding: 20px;
  display: flex;
  position: relative;
}

.contact-details {
  flex-direction: column;
  gap: 50px;
  display: flex;
}

.contact-item {
  align-items: center;
  gap: 15px;
  min-height: 3rem;
  display: flex;
}

.icon {
  background-color: #e8f1ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  display: flex;
}

.icon i {
  color: #06c;
  font-size: 1.5rem;
}

.icon:hover {
  background-color: #06c;
  transform: scale(1.1);
}

.icon:hover i {
  color: #fff;
}

.info h3 {
  color: #2f73b9;
  margin: 0 0 5px;
  font-size: 28px;
  font-weight: 600;
}

.info p {
  color: #d1d5db;
  margin: 0;
  font-size: 20px;
}

.contact-form {
  width: 100%;
  padding: 20px;
}

.form {
  box-sizing: border-box;
  text-align: left;
  background: linear-gradient(to bottom right, #48c6ef, #6f86d6);
  border-radius: 8px;
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  padding: 30px;
  font-size: .95rem;
}

.form-row {
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.form-row input {
  color: #f5f5f5;
  background: #2c3e50;
  border: 1px solid #2f73b9;
  border-radius: 6px;
  flex: 1;
  min-width: 0;
  padding: 12px;
}

textarea {
  color: #f5f5f5;
  resize: vertical;
  box-sizing: border-box;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  background: #2c3e50;
  border: 1px solid #2f73b9;
  border-radius: 6px;
  margin-bottom: 20px;
  padding: 12px;
}

.btn {
  color: #fff;
  cursor: pointer;
  background: #2c3e50;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: 12px 30px;
  font-size: 1rem;
  font-weight: bold;
}

.btn:hover {
  color: #1c3246;
  background: linear-gradient(to right, #4e65ff, #09f);
  font-weight: bold;
}

.galaxy-line {
  z-index: 5;
  background: linear-gradient(to right, #2f73b900, #2f73b9, #2f73b900);
  width: 300px;
  height: 2px;
  animation: 3s ease-in-out infinite glow;
  position: absolute;
  top: calc(130px + 3.7rem);
  left: 10%;
  transform: translateX(-50%);
}

@keyframes glow {
  0% {
    box-shadow: 0 0 10px #2f73b9cc;
  }

  50% {
    box-shadow: 0 0 20px #2f73b9;
  }

  100% {
    box-shadow: 0 0 10px #2f73b9cc;
  }
}

.particle {
  z-index: 6;
  opacity: 1;
  background: #a0d6ee;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  animation: 3s linear infinite particleFlow;
  position: absolute;
  top: 140px;
}

@keyframes flow {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 0 100px;
  }
}

.particle {
  z-index: 5;
  background: #a0d6ee;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  animation: 3s linear infinite particleFlow;
  position: absolute;
  top: 140px;
}

.particle-1 {
  animation-delay: 0s;
  left: 41%;
}

.particle-2 {
  animation-delay: 1s;
  left: 46%;
}

.particle-3 {
  animation-delay: 2s;
  left: 51%;
}

.particle-4 {
  animation-delay: 3s;
  left: 56%;
}

@media (width >= 769px) and (width <= 1200px) {
  .contact-container {
    box-sizing: border-box;
    grid-template-columns: .4fr .6fr;
    gap: 40px;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0;
  }

  .form {
    width: 100%;
    margin: 0;
  }

  .form-row input, textarea {
    width: 100%;
