  .contact-info, .contact-form {
    padding: 15px;
  }

  .form {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
  }

  .form-row {
    flex-direction: column;
  }

  .btn {
    width: 50%;
    margin: 0 auto;
    display: block;
  }
}

